import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ProductInterestRate,SalableProduct } from "app/pages/tzb/valuation/product-interest-allocation/deposit-product-interest-rate/bean/product-interest-rate.bean";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { specialSymbols } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'product-interest-rate',
    templateUrl: './product-interest-rate.component.html',
    styleUrls: ['./product-interest-rate.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class ProductInterestRateComponent implements OnInit {
    code: any;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    firsts: number = 0;
    pageSizes: number = 10;
    pageNums: number = 1;
    totals: number = 0;
    list_Table: any = [];//查询列表
    display: boolean = false;//可售产品弹框控制
    ChineseShow: Boolean = false;//利率名称校验
    productList:  any = [];//可售产品列表
    RateType: any = [];//利率分类码值
    state: any  = [];//状态码值
    baseRateInterestList: any = [];//利率单位码值
    index: any = '0';//控制是否初始化查询
    productinterestrate:ProductInterestRate = new ProductInterestRate();
    salableproduct: SalableProduct = new SalableProduct();//可售产品入参初始化

    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.codeVal();
        this.RateType = [{ label: '请选择', value: '' }, { label: '默认利率', value: 'DEFAULT_PRICE' }, { label: '最低利率', value: '2' }, { label: '特别优惠利率', value: '3' },
        { label: '促销利率', value: '4' }, { label: '参考利率', value: '5' }, { label: '标准利率', value: 'LIST_PRICE' }, { label: '最高利率', value: '7' },];//利率分类
        this.state = [{ label: '请选择', value: '' }, { label: '录入待复核', value: '0' }, { label: '复核生效', value: '1' }, { label: '修改待复核', value: '2' },
        { label: '作废待复核', value: '3' }, { label: '作废', value: '4' }, { label: '复核退回', value: '5' }];//状态
        this.baseRateInterestList = [{ label: '日利率', value: '01' }, { label: '月利率', value: '02' }, { label: '年利率', value: '03' }];//利率单位
        this.index = this.route.snapshot.params['query'];
        if(this.index){
            this.query();
        }
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //弹出可售产品弹框
    chooseProduct(){
        this.display = true;
        this.pquery();
    }
    //可售产品查询
    pquery(){
        this.pageSizes = 10;
        this.pageNums = 1;
        this.firsts = 0;
        this.getProductIdAndName();
    }
    //可售产品弹框界面重置
    preset(){
        this.salableproduct = new SalableProduct();
        this.pquery();
    }
    //可售产品查询接口
    getProductIdAndName(){
        let params = {
            pageSize: this.pageSizes,
            pageNum: this.pageNums,
            productId: this.salableproduct.productId,
            productName: this.salableproduct.productName,
        }
        this.httpService.findAvailableProductList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.productList = data.resultList;
                this.totals = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: '调用服务失败！' });
        })
    }
    //产品选择回调
    choose(car){
        this.productinterestrate.productPriceCode = car.productId;
    }
    //可售产品弹框界面确定
    mainSeve(){
        this.display = false;
    }
    //可售产品弹框界面取消
    goBack(){
        this.display = false;
    }
    //汉字校验
    onChineseCheck() {
        if (this.productinterestrate.rateName != '') {
            if (specialSymbols(null, this.productinterestrate.rateName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else{
            this.ChineseShow = false;
        }
    }
    //查询
    query() {
        if(this.ChineseShow){
            return;
        }
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.selectProductPriceRateMaintenanceList();
    }
    selectProductPriceRateMaintenanceList() {
        if(this.productinterestrate.rateTypeCode && this.productinterestrate.rateTypeCode != null){
            this.selectRateIdAndRateCode();
        }
        let params = {
            productPriceName: this.productinterestrate.productPriceName,//产品名称
            productPriceCode: this.productinterestrate.productPriceCode,//产品编号
            productRateCode: this.productinterestrate.productRateCode,//利率编号
            rateName: this.productinterestrate.rateName,//利率名称
            statusCode: this.productinterestrate.statusCode,//状态
            productRateTypeCode: this.productinterestrate.productRateTypeCode,//利率分类编号
            pageSize: this.pageSize,
            pageNum: this.pageNum,
        }
        this.httpService.selectProductPriceRateMaintenanceList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list_Table = data.productPriceRateMaintenanceList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '产品利率查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '产品利率查询失败', detail: '调用服务失败！' });
        })
    }
    //翻页
    paginate(event) {
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.selectProductPriceRateMaintenanceList();
    }
    //可售产品翻页
    paginates(event) {
        this.pageSizes = event.rows * 1;
        //当前页
        this.pageNums = event.page + 1;
        this.firsts = event.page * this.pageSizes;
        this.getProductIdAndName();
    }
    //通过利率类型编号及名称查询利率编号和利率值			
    selectRateIdAndRateCode(){
        let params = {
            rateTypeCode: this.productinterestrate.rateTypeCode,//利率类型编号
            rateTypeName: this.productinterestrate.rateTypeName,//利率类型名称
        }
        this.httpService.selectRateIdAndRateCode(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.productinterestrate.productRateCode = data.productRateCode;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基础利率值查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基础利率值查询失败', detail: '调用服务失败！' });
        })
    }
    //重置 
    reset(){
        this.productinterestrate = new ProductInterestRate();
        this.ChineseShow = false;
        this.query();
    }
    //新增
    addBtn(){
        this.router.navigate(['/pages/tzb/valuation/product-interest-rate-add',{ title: '产品利率新增'}])
    }
    //修改
    toUpdate(col){
        this.router.navigate(['/pages/tzb/valuation/product-interest-rate-add',{ title: '产品利率修改', data: col.productPriceCode, statusCode: col.statusCode}])
    }
    //详情
    toDetail(col){
        this.router.navigate(['/pages/tzb/valuation/product-interest-rate-add',{ title: '产品利率详情', data: col.productPriceCode, statusCode: col.statusCode }])
    }
    //删除
    toDelete(col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    productPriceCode: col.productPriceCode,//	产品编号
                    productPriceName: col.productPriceName,//	产品名称
                    statusCode: col.statusCode,//复核状态
                };
                this.httpService.removeProductPriceRateMaintenance(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                })
            }
        });
    }
}
