import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import * as _ from 'lodash';
import { ProductInterestRate, SalableProduct } from "app/pages/tzb/valuation/product-interest-allocation/deposit-product-interest-rate/bean/product-interest-rate.bean";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { specialSymbols, TwoInteger } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'product-interest-rate-add',
    templateUrl: './product-interest-rate-add.component.html',
    styleUrls: ['./product-interest-rate-add.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class ProductInterestRateAddComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    firsts: number = 0;
    pageSizes: number = 10;
    pageNums: number = 1;
    totals: number = 0;
    firstes: number = 0;
    pageSizees: number = 10;
    pageNumes: number = 1;
    totales: number = 0;
    display: boolean = false;//可售产品弹出框控制
    Display: boolean = false;//利率弹出框控制
    show: boolean = false;//利率弹出框控制是否是第三方服务
    title: any;//页面标题
    data: any;//父页面传递的数据
    row: any;//记录第几行
    listOptions: any = [];//利率分类类型码值
    RateType: any = [];//利率分类码值
    state: any = [];//状态码值
    interestType: any = [];//利率种类码值
    rateTermList: any = [];//期限码值
    baseRateInterestList: any = [];//利率单位码值
    productRateTypeList: any = [];//利率分类明细列表
    rateTypeNameAndBaseRateList: any = [];//利率弹出框列表
    list_Table: any = [];//利率分类明细展示列表
    productList: any = [];//可售产品列表
    numberShow: Boolean = false;//利率值校验
    ChineseShow: Boolean = false;//利率名称校验
    orgTreeDisplay: Boolean = false;//机构树弹框控制
    isClick: Boolean = false;//提交按钮不可连续多次点击控制
    ownerOrgInValue: any;//机构树inValue
    rateOrgName: any;//机构号对应的机构片区
    productPriceCode: any;//产品编号
    productPriceName: any;//产品名称
    selectedValue: any;//单选数据
    iswork: any;//是否是工作流
    statusCode: any;//复核状态
    productinterestrate: ProductInterestRate = new ProductInterestRate();
    salableproduct: SalableProduct = new SalableProduct();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) { }

    ngOnInit() {
        this.data = this.route.snapshot.params['data'];
        this.title = this.route.snapshot.params['title'];
        this.statusCode = this.route.snapshot.params['statusCode'];
        //修改
        if (this.data && this.data != null) {
            this.selectProductPriceRateMaintenance();
        }
        this.iswork = this.route.snapshot.params['productPriceCode'];
        //工作流
        if (this.route.snapshot.params['productPriceCode']) {
            //查询
            this.httpService.selectProductPriceRateMaintenance({ productPriceCode: this.route.snapshot.params['productPriceCode'], statusCode: this.statusCode }).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.productPriceCode = data.productPriceCode;
                    this.productPriceName = data.productPriceName;
                    this.productRateTypeList = data.productRateTypeList;
                    for (let i = 0; i < this.productRateTypeList.length; i++) {
                        this.productRateTypeList[i].baseRateOrCustomMethodCode = data.productRateTypeList[i].productRateCode;
                    }
                    this.total = this.productRateTypeList.length;
                    this.paginate({ page: 0, first: 0, rows: "10", pageCount: Math.ceil(this.total / 10) });
                }
            }, error => {
            })
        }
        this.codeVal();
        this.getProductIdAndName();
        this.selectProductCondValue();
        this.listOptions = [{ label: '基础利率', value: '1' }, { label: '基准利率', value: '3' }, { label: '市场利率', value: '4' }, { label: '第三方服务', value: '2' }];//利率类型名称
        this.RateType = [{ label: '请选择', value: '' }, { label: '默认利率', value: 'DEFAULT_PRICE' }, { label: '最低利率', value: '2' }, { label: '特别优惠利率', value: '3' },
        { label: '促销利率', value: '4' }, { label: '参考利率', value: '5' }, { label: '标准利率', value: 'LIST_PRICE' }, { label: '最高利率', value: '7' },];//利率分类
        this.interestType = [{ label: '请选择', value: '' }, { label: '存款活期利率', value: '01' }, { label: '存款定期利率', value: '02' }, { label: '贷款利率', value: '03' }];//利率种类
        this.state = [{ label: '请选择', value: '' }, { label: '录入待复核', value: '0' }, { label: '复核生效', value: '1' }, { label: '修改待复核', value: '2' },
        { label: '作废待复核', value: '3' }, { label: '作废', value: '4' }, { label: '复核退回', value: '5' }];//状态
        this.baseRateInterestList = [{ label: '日利率', value: '01' }, { label: '月利率', value: '02' }, { label: '年利率', value: '03' }];//利率单位
        this.productinterestrate.enumTypeCode = 'DEFAULT_PRICE';
        this.productinterestrate.rateClassify = '1';
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //期限码值
    selectProductCondValue() {
        let params = {
            conditionalDimensionCode: 'baseRateTerm'
        }
        this.httpService.selectProductCondValueList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": null });
                data.enumerationValueList.forEach((v) => {
                    List.push({ "label": v.dimensionValue, "value": v.dimensionCode });
                })
                this.rateTermList = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询失败', detail: '调用服务失败！' });
        })
    }
    //修改页面数据初始化
    selectProductPriceRateMaintenance() {
        let params = {
            productPriceCode: this.data,//产品编号
            statusCode: this.statusCode//复核状态
        }
        this.httpService.selectProductPriceRateMaintenance(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.productPriceCode = data.productPriceCode;
                this.productPriceName = data.productPriceName;
                this.productRateTypeList = data.productRateTypeList;
                for (let i = 0; i < this.productRateTypeList.length; i++) {
                    this.productRateTypeList[i].baseRateOrCustomMethodCode = data.productRateTypeList[i].productRateCode;
                }
                this.total = this.productRateTypeList.length;
                this.paginate({ page: 0, first: 0, rows: "10", pageCount: Math.ceil(this.total / 10) });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '产品利率查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '产品利率查询失败', detail: '调用服务失败！' });
        })
    }
    //弹出可售产品弹框
    chooseProduct() {
        this.display = true;
        this.pquery();
    }
    //可售产品查询
    pquery() {
        this.pageSizes = 10;
        this.pageNums = 1;
        this.firsts = 0;
        this.getProductIdAndName();
    }
    //可售产品重置
    preset() {
        this.salableproduct = new SalableProduct();
        this.pquery();
    }
    //可售产品码值
    getProductIdAndName() {
        let params = {
            pageSize: this.pageSizes,
            pageNum: this.pageNums,
            productId: this.salableproduct.productId,
            productName: this.salableproduct.productName,
        }
        this.httpService.findAvailableProductList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.productList = data.resultList;
                this.totals = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: '调用服务失败！' });
        })
    }
    //产品选择回调
    choose(car) {
        this.productPriceCode = car.productId;
        this.productPriceName = car.productName;
    }
    //可售产品弹框确认
    mainSeve() {
        this.display = false;
    }
    //可售产品弹框返回
    goBack() {
        this.display = false;
    }
    //可售产品列表翻页
    paginates(event) {
        this.pageSizes = event.rows * 1;
        //当前页
        this.pageNums = event.page + 1;
        this.firsts = event.page * this.pageSizes;
        this.getProductIdAndName();
    }
    //利率弹出框翻页
    paginatees(event) {
        this.pageSizees = event.rows * 1;
        //当前页
        this.pageNumes = event.page + 1;
        this.firstes = event.page * this.pageSizees;
        if (this.productinterestrate.rateClassify != '2') {
            this.show = false;
            this.rateTypeNameAndBaseRateList = [];
            this.selectRateTypeNameAndBaseRateList();
        } else if (this.productinterestrate.rateClassify == '2') {
            this.show = true;;
            this.rateTypeNameAndBaseRateList = [];
            this.selectCustomMethodList();
        }
    }
    //利率弹出框查询
    query() {
        if (this.numberShow || this.ChineseShow) {
            return;
        }
        this.firstes = 0;
        this.pageSizees = 10;
        this.pageNumes = 1;
        if (this.productinterestrate.rateClassify != '2') {
            this.show = false;
            this.rateTypeNameAndBaseRateList = [];
            this.selectRateTypeNameAndBaseRateList();
        } else if (this.productinterestrate.rateClassify == '2') {
            this.show = true;;
            this.rateTypeNameAndBaseRateList = [];
            this.selectCustomMethodList();
        }
    }
    //机构树
    openOrgId(param) {
        this.orgTreeDisplay = true;
        this.Display = false;
        this.ownerOrgInValue = 'adjustOrg'
    }
    //机构树弹框关闭
    onHideClose() {
        this.orgTreeDisplay = false;
        this.Display = true;
    }
    //机构树回调
    treeShow(param) {
        this.orgTreeDisplay = false;
        this.Display = true;
        this.productinterestrate.rateOrgId = param.orgId;
        this.rateOrgName = param.orgName;
    }
    //利率类型名称及基础利率值查询
    selectRateTypeNameAndBaseRateList() {
        let params = {
            rateName: this.productinterestrate.rateName,//	利率名称
            rateClassify: this.productinterestrate.rateClassify,//	利率分类类型
            rateSort: this.productinterestrate.rateSort,//	利率种类
            rateTerm: this.productinterestrate.rateTerm,//	期限
            orgId: this.productinterestrate.rateOrgId,//机构
            productBaseRateValue: this.productinterestrate.productBaseRateValue,//利率值
            statusCode: this.productinterestrate.statusCode,//状态
            pageSize: this.pageSizees,
            pageNum: this.pageNumes,
        }
        this.httpService.selectRateTypeNameAndBaseRateList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.rateTypeNameAndBaseRateList = data.rateTypeNameAndBaseRateList;
                this.totales = data.totalNum;
                for (let l = 0; l < this.rateTypeNameAndBaseRateList.length; l++) {
                    this.rateTypeNameAndBaseRateList[l].fromDate = this.commfunc.transDateN(this.rateTypeNameAndBaseRateList[l].fromDate);
                    this.rateTypeNameAndBaseRateList[l].thruDate = this.commfunc.transDateN(this.rateTypeNameAndBaseRateList[l].thruDate);
                    if (this.rateTypeNameAndBaseRateList[l].orgId && this.rateTypeNameAndBaseRateList[l].orgId != null) {
                        this.httpService.queryOrgById({ orgId: this.rateTypeNameAndBaseRateList[l].orgId }).subscribe(data => {//根据机构查询机构名
                            if (data.returnCode.code == 'success') {
                                this.rateTypeNameAndBaseRateList[l].rOrgName = data.orgName;
                            }
                            else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                            }
                        }, err => {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                        });
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '利率类型查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '利率类型查询失败', detail: '调用服务失败！' });
        })
    }

    //查询第三方服务列表					
    selectCustomMethodList() {
        let params = {
            customMethodName: this.productinterestrate.customMethodName,//第三方服务中文名称
            pageSize: this.pageSizees,
            pageNum: this.pageNumes,
        }
        this.httpService.selectCustomMethodList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.rateTypeNameAndBaseRateList = data.customMethodList;
                this.totales = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询第三方服务失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询第三方服务失败', detail: '调用服务失败！' });
        })
    }
    // 数字校验
    onNumberCheck() {
        if (this.productinterestrate.productBaseRateValue != '') {
            if (TwoInteger(null, this.productinterestrate.productBaseRateValue)) {
                this.numberShow = true;
            } else {
                this.numberShow = false;
            }
        } else {
            this.numberShow = false;
        }
    }
    //汉字校验
    onChineseCheck() {
        if (this.productinterestrate.rateName != '') {
            if (specialSymbols(null, this.productinterestrate.rateName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else {
            this.ChineseShow = false;
        }
    }
    //保存
    save() {
        this.isClick = true;
        let i = 0
        for (i; i <= this.productRateTypeList.length - 1; i++) {
            if (this.productRateTypeList[i].productRateTypeCode == 'DEFAULT_PRICE') {
                break;
            }
        }
        if (i == this.productRateTypeList.length) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '利率分类明细中缺少一条默认利率!' });
            this.isClick = false;
            return;
        }
        //产品编号不能为空
        if (!this.productPriceCode) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '产品编号不能为空' });
            this.isClick = false;
            return;
        }
        //产品名称不能为空
        if (!this.productPriceName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '产品名称不能为空' });
            this.isClick = false;
            return;
        }
        let params = {
            productPriceCode: this.productPriceCode,//	产品编号
            productPriceName: this.productPriceName,//	产品名称
            statusCode: this.statusCode,//复核状态
            productRateTypeList: this.productRateTypeList,//	利率分类明细列表
        }
        if (this.title == '产品利率新增') {
            this.httpService.createProductPriceRateMaintenance(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '产品利率新增成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/product-interest-rate', { query: '1' }])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '产品利率新增失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '产品利率新增失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        } else if (this.title == '产品利率修改') {
            this.httpService.updateProductPriceRateMaintenance(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '产品利率修改成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/product-interest-rate', { query: '1' }])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '产品利率修改失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '产品利率修改失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        }
    }
    //翻页
    paginate(event) {
        console.log(event);
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.list_Table = this.productRateTypeList.slice(this.first, this.first + 10);
    }
    //增加一条
    addBtn() {
        let aa = _.clone(this.productRateTypeList);
        aa.unshift({
            enumTypeCode: '', productRateTypeCode: '', productRateTypeName: '', baseRateOrCustomMethodCode: ''
        });
        this.total++;
        this.productRateTypeList = aa;
        this.paginate({ page: 0, first: 0, rows: "10", pageCount: Math.ceil(this.total / 10) });
    }
    //删除
    toDelete(i) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                this.productRateTypeList.splice(this.first + i, 1);
                this.total--;
                this.productRateTypeList = _.clone(this.productRateTypeList);
                if (this.productRateTypeList.slice(this.first, this.first + 10).length != 0) {
                    this.list_Table = this.productRateTypeList.slice(this.first, this.first + 10);
                } else {
                    this.paginate({ page: this.total / 10 - 1, first: this.total - 10, rows: "10", pageCount: this.total / 10 });
                }
            }
        });
    }
    //返回
    return() {
        this.router.navigate(['/pages/tzb/valuation/product-interest-rate', { query: '1' }])
    }
    //弹出利率弹出框
    selectProductCondValueList(index, item) {
        if (item && item != null) {
            this.Display = true;
            this.row = index;
        }
        this.productinterestrate = new ProductInterestRate();
        this.selectedValue = null;
        this.productinterestrate.rateClassify = '1';
        this.query();
    }
    //单选赋值
    select(col) {
        if (this.productinterestrate.rateClassify != '2') {
            this.productRateTypeList[this.row].enumTypeCode = '1';
            this.productRateTypeList[this.row].classificationOfRatesName = col.rateName;
            this.productRateTypeList[this.row].productBaseRateValue = col.productBaseRateValue;
            this.productRateTypeList[this.row].baseRateOrCustomMethodCode = col.productRateCode;
        } else if (this.productinterestrate.rateClassify == '2') {
            this.productRateTypeList[this.row].enumTypeCode = '2';
            this.productRateTypeList[this.row].classificationOfRatesName = col.customMethodName;
            this.productRateTypeList[this.row].productBaseRateValue = col.customMethodCode;
            this.productRateTypeList[this.row].baseRateOrCustomMethodCode = col.customMethodId;
        }
        this.Display = false;
    }
    //重置
    reset() {
        this.productinterestrate.rateClassify = '1';
        this.productinterestrate.rateName = null;
        this.productinterestrate.rateSort = null;
        this.productinterestrate.rateTerm = null;
        this.productinterestrate.branchId = null;
        this.productinterestrate.productBaseRateValue = null;
        this.productinterestrate.customMethodName = null;
        this.query();
    }
    //关闭利率弹出框
    cancel() {
        this.Display = false;
    }
}
